import {useState} from 'react'
import './App.scss';
import Header from './Components/Header'
import Home from './Components/Home'
import Market from './Components/Market'
import EventGallery from './Components/EventGallery'
import Footer from './Components/Footer'
import { BrowserRouter as Router, Redirect, Route } from 'react-router-dom';

function App() {

  let [darkMode, setDarkMode] = useState(localStorage.getItem("mode") === "true")

  return (
    <div className="App">
      <Router>
        <Header darkMode={darkMode} setDarkMode={setDarkMode} />
        <div className="content">
          <Route exact path="/" component={() => (<Redirect to='/media_eye' />)} />
          <Route path="/media_eye" render={() => (
            <Home darkMode={darkMode}/>
          )}/>
           <Route path="/market" render={() => (
            <Market darkMode={darkMode}/>
          )}/>  
           <Route path="/gallery" render={() => (
            <EventGallery darkMode={darkMode}/>
          )}/>                       
        </div>
        <Footer darkMode={darkMode} />
      </Router>
    </div>
  );
}

export default App;
